.portfolio{
    position: relative;
    }
    .portfolio .box{
        border-radius: 10px;
        position: relative;
    }
    .portfolio .box:hover{
        background: none;
        transform: translateY(0);
    }
    .portfolio .img {
        height: 300px;
        overflow: hidden;
        transition: 0.5s;

    }
    .portfolio img {
        width: 100%;
        height: 90%;
        border-radius: 10px;
        transition: 0.5s;
    }
    .portfolio .box .img:hover img{
        transform: scale(1.2);
    }
    .portfolio .category{
        margin: 20px 0 30px 0;
        text-transform: uppercase;


    }
    .portfolio .category span{
        color: #ff014f;
        font-size: 13px;
        letter-spacing: 2px;
        font-weight: 500;
    }
    .portfolio .arrow{
        position: absolute;
        right: 30px;
        bottom: 11px;
        transform: rotate(-45deg);
        color: #ff014f;
        opacity: 0;
        transition: 0.5s;

    }
    .portfolio .title:hover .arrow{
        opacity: 1;
    }
    .portfolio h2{
        font-size: 18px;
        transition: 0.5s;
        padding-bottom: 10px;
    }
    .portfolio .box:hover h2{
        color: #ff014f;
    }
    .portfolio .box:hover {
        color: black;
    }
    .portfolio label{
        margin-left: 10px;

    }
    /*-------------popup----------*/
  
    .btn-model{
        padding: 10px 20px;
        display: block;
        margin: 100px auto 0;
        font-size: 18px;
    }
    .model,
    .overlay{
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        position: fixed;
        z-index: 9999;

    }
    .overlay{
        background-color:#ecf0f3;
    }

    .model-content{
        position: absolute;
        top: 8%;
        left: 7%;
        line-height: 1.4;
        background: white;
        padding: 14px 28px;
        min-width: 60%;
        max-width: 85%;
        z-index: 9999;
        box-shadow: 4px 4px 8px #cbcbcb , 4px -4px 8px #ffffff;
        border-radius: 10px;

    }
    .model-text{
        padding: 0 100px 0 50px;
    }

    .model-text h1{
        margin: 20px 0 20px 0;
        font-size: 30px;
    }
    .model-text p{
        font-size: 17px;
        margin-bottom: 20px;
        line-height: 25px;
    }
    .model-text button{
        margin-right: 20px;
        color: #ff014f;
        margin-bottom: 25px;
        
        
    }
    .model-text button i{
        margin-left: 10px;
        color: #ff014f;
        


    }
    .model-text button.close-model {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 50px;
        height: 50px;
        text-align: center;
        border-radius: 50%;
        padding: 0 0;
        font-size: 20px;
    }
    .model-text button.close-model i {
        margin: 0;
    }
  /*-------------popup----------*/